.clear-fix {
  display: "block";
  zoom: 1;
}
.clear-fix::after {
  content: ".";
  height: 0px;
  display: block;
  visibility: hidden;
  clear: both;
}
* {
  font-size: 14px;
  color: #4d4d4d;
}
.con-commentapp_div {
  margin: 10px auto;
  width: 600px;
  padding: 10px;
  border: 1px solid #e4e4e4;
  border-radius: 5px;
}
.con-commentinput_div {
  border: 1px solid #e4e4e4;
  border-radius: 5px;
  padding: 20px 30px;
}
.con-commentinput_div div {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 15px;
}
.con-commentinput_div div:last-child {
  margin-bottom: 0px;
}
.con-commentinput_div .user-field_div {
  position: relative;
}
.con-commentinput_div .user-field_div span {
  -ms-flex-preferred-size: 100px;
      flex-basis: 100px;
}
.con-commentinput_div .user-field_div input {
  padding: 5px;
  -ms-flex: 1 1;
      flex: 1 1;
}
.con-commentinput_div .word-field_div span {
  -ms-flex-preferred-size: 100px;
      flex-basis: 100px;
}
.con-commentinput_div .word-field_div textarea {
  padding: 5px;
  resize: none;
  -ms-flex: 1 1;
      flex: 1 1;
}
.con-commentinput_div .submit-field_div {
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.con-commentinput_div .submit-field_div button {
  float: right;
  color: white;
  background-color: #3399CC;
  border: none;
  outline: none;
  padding: 3px 30px;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}
.con-commentlist_div {
  margin: 20px;
  padding-bottom: 30px;
}
.con-comment_div {
  position: relative;
  word-break: break-all;
  margin-top: 20px;
  padding-bottom: 40px;
  border-bottom: 1px solid #e4e4e4;
}
.con-comment_div span.name-comment_span {
  color: #25c6fc;
}
.con-comment_div code {
  padding-left: 10px;
  padding-right: 10px;
  background-color: #e8e8e8;
  border-radius: 2px;
}
.con-comment_div div.field-comment_div {
  position: relative;
  margin-top: 20px;
  display: "block";
  zoom: 1;
}
.con-comment_div div.field-comment_div::after {
  content: ".";
  height: 0px;
  display: block;
  visibility: hidden;
  clear: both;
}
.con-comment_div div.field-comment_div span.delete-comment_span {
  float: left;
  margin-left: 400px;
  font-size: 14px;
  color: #ff534d;
  cursor: pointer;
}
.con-comment_div div.field-comment_div span.time-comment_span {
  float: right;
  font-size: 14px;
  color: grey;
  margin-right: 20px;
}



